const DEFAULT_INTERVAL = 16;
const DEFAULT_STEP = 1;

export default class Frame {
    constructor(containerWidth, element, step, updateInterval) {
        this.containerWidth = containerWidth;
        this.element = element;
        this.step = step || DEFAULT_STEP;
        this.updateInterval = updateInterval || DEFAULT_INTERVAL;

        this.interval = null;

        this.width = 0;
        this.marginLeft = 0;
        this.offsetLeft = 0;
    }

    init() {
    }

    run() {
        if (this.interval) {
            this.stop();
        }

        this.init();
        this.interval = setInterval(this.onDraw.bind(this), this.updateInterval);
    }

    stop() {
        if (this.interval) {
            clearInterval(this.interval);
            this.interval = null;
        }
    }

    measure() {
        this.width = this.element.offsetWidth; // getBoundingClientRect().width;
        this.marginLeft = parseInt(this.element.style.marginLeft, 10) || 0;
        this.offsetLeft = this.element.offsetLeft;
    }

    onDraw() {
        this.measure();

        this.marginLeft -= this.step;
        if (Math.abs(this.marginLeft) > this.containerWidth) {
            this.marginLeft = 0;
        }

        this.element.style.marginLeft = `${this.marginLeft}px`;
    }
};
