import Frame from './frame';

export default class RepeatFrame extends Frame {
    constructor(containerWidth, element, step, updateInterval) {
        super(containerWidth, element, step, updateInterval);

        this.objects = element.children;
        this.firstObject = this.objects[0];
        this.objectWidth = this.firstObject.offsetWidth;
        this.offsetLeft = 0;
    }

    measure() {
        this.width = this.element.getBoundingClientRect().width;
        this.marginLeft = parseInt(this.firstObject.style.marginLeft, 10) || 0;
        this.offsetLeft = this.element.offsetLeft;
    }

    onDraw() {
        // this.measure();

        this.marginLeft -= this.step;
        if (Math.abs(this.marginLeft) > this.objectWidth) {
            this.marginLeft = 0;

            // Move to end
            this.firstObject.style.marginLeft = 0;
            this.element.appendChild(this.objects[0]);
            this.firstObject = this.objects[0];
        }

        this.firstObject.style.marginLeft = `${this.marginLeft}px`;
    }
}