import Frame from './frame';
import { RENDERING_PERFORMANCE_FORCE_LAYOUT } from '../basic/common';

const DEFAULT_STEP = 1;

export default class AnimationFrame extends Frame {
    constructor(containerWidth, element, x, movingHeight, step) {
        super(containerWidth, element);

        this.x = x || 0;
        this.y = movingHeight ? parseInt(Math.random() * 1000 % movingHeight, 10) : 0;
        this.movingHeight = movingHeight || 0;
        this.step = step || DEFAULT_STEP;
        this.xStep = this.step;
        this.yStep = parseInt(Math.random() * 100, 10) % this.step || DEFAULT_STEP;

        this.init();
    }

    init() {
        this.objects = Array.prototype.slice.call(this.element.children);
        this.shownObjectIndex = parseInt(Math.random() * 100 % this.objects.length, 10);
        this.shownObject = this.objects[this.shownObjectIndex];

        this.objects.forEach(obj => {
            if (obj !== this.shownObject) {
                obj.classList.add('invisible');
            }
        });

        this.element.style.left = `${this.x}px`;
        if (this.movingHeight) {
            this.element.style.top = `${this.y}px`;
        }
    }

    onDraw() {
        let offsetLeft;
        let offsetTop;
        let containerWidth;
        let absY;

        if (RENDERING_PERFORMANCE_FORCE_LAYOUT) {
            offsetLeft = this.x;
            offsetTop = this.y;
            containerWidth = this.containerWidth;
        } else {
            offsetLeft = this.element.offsetLeft || this.x;
            offsetTop = this.element.offsetTop || this.y;
            containerWidth = this.element.parentElement.offsetWidth;
        }

        absY = Math.abs(offsetTop);

        for (let i = 0; i < this.objects.length; i += 1) {
            const obj = this.objects[i];

            if (this.shownObjectIndex === i) {
                obj.classList.remove('invisible');
            } else if (obj.parentElement) {
                obj.classList.add('invisible');
            }
        }

        // x 축 이동
        if (offsetLeft > containerWidth) {
            offsetLeft = 0;
            this.xStep = parseInt(Math.random() * 100, 10) % (this.step*2) || DEFAULT_STEP;
        }
        offsetLeft += this.xStep;
        this.element.style.left = `${offsetLeft}px`;

        // y 축 이동
        if (this.movingHeight) {
            if (absY > this.movingHeight || offsetTop < 0) {
                this.yStep *= -1;
            }
            offsetTop += this.yStep;
            this.element.style.top = `${offsetTop}px`;
        }

        this.x = offsetLeft;
        this.y = offsetTop;

        this.shownObject = this.objects[this.shownObjectIndex];
        this.shownObjectIndex += 1;

        if (this.shownObjectIndex >= this.objects.length) {
            this.shownObjectIndex = 0;
        }
    }

    clone(x, step) {
        const parent = this.element.parentElement;
        const cloneFrameElement = this.element.cloneNode();
        this.objects.forEach(object => {
            cloneFrameElement.appendChild(object.cloneNode(true));
        });
        parent.appendChild(cloneFrameElement);

        return new AnimationFrame(this.containerWidth, cloneFrameElement, x, this.movingHeight, step);
    }
}