import {LOADING_PERFORMANCE_SPRITE_IMAGES} from '../basic/common'

export function loadImageByPromise(parentElement, classList, src, frameset) {
    if (LOADING_PERFORMANCE_SPRITE_IMAGES) {
        return addImageClassByPromise(parentElement, classList, src, frameset);
    }

    return new Promise(resolve => {
        const img = document.createElement('img');

        classList.forEach(className => {
            img.classList.add(className);
        });
        img.src = src;
    
        parentElement.appendChild(img);
    
        img.onload = () => {
            resolve({
                frameParent: parentElement,
                frameset
            });
        };
    });
}

function addImageClassByPromise(parentElement, classList, src, frameset) {
    return new Promise(resolve => {
        const img = document.createElement('span');

        classList.forEach(className => {
            img.classList.add(className);
        });
    
        parentElement.appendChild(img);
    
        resolve({
            frameParent: parentElement,
            frameset
        });
    });
}
