import 'css/main.css';
import basic from 'js/basic';

document.addEventListener('DOMContentLoaded', () => {
    const container = document.getElementById('container');
    
    basic.generate(container);
});

function loadMainComponentByPreRender() {
    const html = `
    <h3 id="header">NHN Forward 2018</h3>
    <div id="container" class="container-el">
    </div>
    <h3 id="footer">FE Development Labs</h3>
    `;
    
    return html;
}

export default loadMainComponentByPreRender;
