const DEFAULT_INTERVAL = 100;

export default class FrameSet {
    constructor(updateInterval) {
        this.frames = [];
    }

    addFrame(frame) {
        this.frames.push(frame);
    }

    init() {
        this.frames.forEach(frame => {
            frame.init();
        });
    }

    onDraw() {
        this.frames.forEach(frame => {
            frame.onDraw();
        });
    }

    clone() {
        const newFrameSet = new FrameSet();

        this.frames.forEach(frame => {
            newFrameSet.addFrame(frame.clone());
        });

        return newFrameSet;
    }
};